import { FinancialCard } from '@components/FinancialCard';
import { MainLoader } from '@components/Loaders/MainLoader';
import { useCachableStoreRequest } from '@hooks/useCachableStoreRequest';
// import { useCurrencies } from '@hooks/useCurrencies';
import { selectCurrenciesState } from '@store/selectors/currencySelector';
import { fetchCurrenciesThunk } from '@store/slices/currencySlice';

import { Currency } from '@/types/financial';

import { sectionTitles } from './config';
import { FinancialSection, GridContainer, SectionTitle } from './styled';

export const Financial = () => {
  const {
    data: currencies,
    isLoading,
    error,
    lastUpdated,
  } = useCachableStoreRequest<Currency[]>({
    cacheInterval: 60 * 1000,
    selector: selectCurrenciesState,
    thunk: fetchCurrenciesThunk,
  });

  if (isLoading) {
    return <MainLoader />;
  }
  if (error) {
    return <p>{error}</p>;
  }

  return (
    <FinancialSection>
      <h1>Last updated: {lastUpdated}</h1>

      {/* TODO[https://sharing.clickup.com/9012257271/t/h/86963qw0b/EXLR3DQ7I2EIAJI] - format time string */}
      <SectionTitle>{sectionTitles.stocks}</SectionTitle>

      {/* TODO[https://sharing.clickup.com/9012257271/t/h/86963qw0b/EXLR3DQ7I2EIAJI] - add stock section */}

      {/* <GridContainer>
        {stocks.map(({ id, ...stock }) => (
          <FinancialCard key={id} financialItem={stock} />
        ))}
      </GridContainer> */}
      <SectionTitle>{sectionTitles.quotes}</SectionTitle>
      {currencies && (
        <GridContainer>
          {currencies.map(({ asset_id, ...currency }) => (
            <FinancialCard key={asset_id} financialItem={currency} />
          ))}
        </GridContainer>
      )}
    </FinancialSection>
  );
};
