import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { AppDispatch } from '@/store';
import { UseCachableStoreRequestProps } from '@/types/hooks';

export const useCachableStoreRequest = <T>({
  cacheInterval = 60 * 1000,
  selector,
  thunk,
}: UseCachableStoreRequestProps<T>) => {
  const dispatch = useDispatch<AppDispatch>();

  const { data, isLoading, error, lastUpdated } = useSelector(selector);

  useEffect(() => {
    const fetchData = async () => {
      await dispatch(thunk());
    };

    if (!data || (Array.isArray(data) && data.length === 0)) {
      fetchData();
    }

    const intervalId = setInterval(fetchData, cacheInterval);

    return () => clearInterval(intervalId);
  }, [dispatch, data, cacheInterval, thunk]);

  return { data, isLoading, error, lastUpdated };
};
